*{
	margin:0;
	border:0;
}
body{
	background-color:#2ED5FF;
	height: 100%;
	max-width:100% ;
}
.bbbb{height: 100%;width:100%;
padding:0 5%;}
.Container{
	width: 90%;
	display:flex;
	flex-direction:column;
	overflow:auto;
	height:50%;}
.recieve{
	align-self:flex-start;
	display:block;
	background-color:#ff8928;
	padding:2%;
	margin:2%;
	border-radius:4%;
}
.send{
	align-self: flex-end;
	display:block;
	background-color:#ff8928;
	padding:2%;
	margin:2%;
	border-radius:4%;
}
#cht-form{
	width:90%;
	position:absolute;
	bottom:12%;
}
.cht-inp{
	padding-top: 0.3rem;
	padding-bottom:0.3rem;
	width:100%;
	height:6%;
	border-radius:2%;
}
.msg-send-button{
	height:6%;
	padding-top: 0.3rem;
	padding-bottom:0.3rem;
	border-radius:2%;
	width:fit-content;
	background-color: #4CAF50;
}
.send-div{
	width:90%;
}
.usernameContainer{
	margin:0 10%;
	display: flex;
	flex-direction:column;
	height: 70%;
	overflow: auto;
}
.usernameButton{
	padding: 1% 2%;
	margin: 2%;
	background-color:#4CAF50;
	width: fit-content;
	display:flex;
	border-radius:0.5rem;
	align-items: center;
}
.opendiv{
	height:100%;
	width:100%;
}
.message-box{
	margin:auto;
}
.searchForm{
	margin:auto;
	width:50%;
	display:flex;
}
.searchInp{
	padding:0.3rem;
	margin: 0 1%;
	width:100%;
	border-radius: 0.5rem;
}
.searchButton{
	height:6%;
	padding-top: 0.3rem;
	padding-bottom:0.3rem;
	border-radius:0.5rem;
	width:fit-content;
	background-color: #4CAF50;
}
.logoutButton{
	color:white;
	background-color:#4CAF50;
	padding:0.5rem;
}